html, body{
  background: black;
  color:#f5183a;
  margin: 0;
  padding: 0;
  padding-top: 10px;
  font-family: 'Press Start 2P', cursive;
  text-align: center;
}

body{
  font-weight: 400;
}

button {
  font-family: 'Press Start 2P', cursive;
  display: block;
  margin: 0 auto;
  border: none;
  background: #f5183a;
  padding: 10px 20px;
  text-transform: uppercase;

}

button:disabled {
  cursor: not-allowed;
  background-color: #a5a2a2;
}

h1 {
  font-size: 20px;
  text-align: center;
}

textarea {
  font-family: 'Press Start 2P', cursive;
  border: 3px solid black;
  width: 60%;
  margin: 0 auto;
  outline: none;
  height: 200px;
  background:  #f5183a;
  padding: 10px;
}

textarea:disabled {
  background-color: #a5a2a2;
}


@media (max-width: 400px){
  textarea{
      height: 200px;
      margin: 5% auto;
    }
    h1{
      font-size: 15px;
  }
  button{
    margin: 20px auto;
  }
  
   
}